import Groups from "./Groups";
import { CookiesProvider } from "react-cookie";
import { useCookies } from "react-cookie";
import { CookieResultManager } from "../../util/CookieResultManager";

const GroupStage = (props) => {
  const [cookies, setCookie] = useCookies(["result"]);
  function resultHandler(picks, group) {
    let result = cookies.result;
    result = CookieResultManager(result, picks, group);
    setCookie("result", result, { path: "/" });
  }

  return (
    <CookiesProvider>
      <Groups
        onResult={resultHandler}
        groupsData={props.groupsData}
        teamsSelected={props.groupsData.teamsSelected}
        setLoadBracket={props.setLoadBracket}
      />
    </CookiesProvider>
  );
};

export default GroupStage;
