import { useState } from "react";
import Group from "./Group";
import "./Groups.css";

const Groups = (props) => {
  const moveHandler = (position) => {
    const element = document.querySelector(".groups-container");
    element.style.transform = `translateX(-${100 * position}%)`;
  };

  const [teamsSelected, setTeamsSelected] = useState(props.teamsSelected);
  const teamsSelectedHandler = (selected) => {
    const newTeamsSelected = selected ? teamsSelected + 1 : teamsSelected - 1;
    setTeamsSelected(newTeamsSelected);
  };

  const clickHandler = () => {
    const element = document.querySelector(".groups");
    element.style.transform = "translateY(-100%)";
    setTimeout(() => props.setLoadBracket(teamsSelected === 16), 500);
  };

  return (
    <div className="groups overflow-hidden">
      <div className="w-full mt-2">
        <div
          className={`groups-progress py-px w-${teamsSelected}/16 bg-emerald-400`}
        ></div>
        <div className="w-0/16 w-1/16 w-2/16 w-3/16 w-4/16 w-5/16 w-6/16 w-7/16 w-8/16 w-9/16 w-10/16 w-11/16 w-12/16 w-13/16 w-14/16 w-15/16 w-16/16"></div>
      </div>
      <div className="groups-container flex flex-nowrap flex-row groups-container-transition">
        {props.groupsData.map((group, i) => (
          <Group
            teams={group.teams}
            group={group.group}
            key={group.group}
            position={i}
            onMove={moveHandler}
            onResult={props.onResult}
            onTeamSelected={teamsSelectedHandler}
            totalTeamsSelected={props.teamsSelected}
          />
        ))}
      </div>
      {teamsSelected === 16 ? (
        <div
          className="to-knockout-rounds absolute bottom-4 w-full cursor-pointer"
          onClick={clickHandler}
        >
          <div className="to-knockout-rounds-text text-slate-400 text-md font-semibold tracking-wide animate-bounce cursor-pointer">
            Proceed to the Knockout Stage...
          </div>
          <div className="arrow">
            <span></span>
            <span></span>
          </div>
        </div>
      ) : null}
    </div>
  );
};

export default Groups;
